<template>
  <section id="checkpaper" class="main clearfix">
    <div class="title">
      <span><i class="el-icon-d-arrow-right"></i></span>
      <div class="fr"><el-button @click="$router.back(-1)" round type="primary" plain></el-button></div>
    </div>
    <aside class="aside-bar">
      <div class="list-title"><span></span> <i id="test-title"></i></div>
      <div class="check-btn">
        <el-button-group>
          <el-button type="primary" @click="subPass"><i class="el-icon-check"></i></el-button>
          <el-button type="primary" @click="subNoPass"><i class="el-icon-close"></i></el-button>
        </el-button-group>
      </div>
    </aside>
    <div class="check-wrap">
      <div class="item">
        <p class="item-title"></p>
        <p class="item-con"><el-button @click="readPosition(paperdata.positionid)" plain type="success"><i class="el-icon-view"></i> {{paperdata.positionname}}</el-button></p>
      </div>
      <div class="item">
        <p class="item-title"></p>
        <p class="item-con">{{paperdata.papername}}</p>
      </div>
      <div class="item">
        <p class="item-title"></p>
        <p class="item-con">{{paperdata.opentime}}</p>
      </div>
      <div class="item">
        <p class="item-title"></p>
        <p class="item-con">{{paperdata.paperdesc}}</p>
      </div>
      <p class="check-status pass"><i class="el-icon-check"></i></p>
      <!-- <p class="check-status nopass"><i class="el-icon-close"></i></p> -->
    </div>
  </section>
</template>
<script>
export default {
  data () {
    return {
      paperdata: {
        positionid: '12312321',
        positionname: '',
        papername: '',
        opentime: '2018-9-9 ~ 2018-10-10',
        paperdesc: ''
      }
    }
  },
  methods: {
    // 
    readPosition (id) {
      console.log(id);
    },
    // 
    subPass () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // TODO
        console.log('pass');
        /* this.$ajax.post(this.Const.apiUrl, {
          module: '',
          controller: '',
          action: '',
          id: 'id'
        }),then(response => {
          console.log('', response);
        }).catch(error => {
          this.$message({type: 'error', message: ''});
        }); */
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });          
      });
    },
    // 
    subNoPass () {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /\S/,
        inputErrorMessage: ''
      }).then(({ value }) => {
        this.$message({
          type: 'success',
          message: ': ' + value
        });
        // TODO
        /* this.$ajax.post(this.Const.apiUrl, {
          module: '',
          controller: '',
          action: '',
          id: 'id'
        }),then(response => {
          console.log('', response);
        }).catch(error => {
          this.$message({type: 'error', message: ''});
        }); */
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });       
      });
    }
  }
}
</script>
<style lang="scss">
#checkpaper {
  background-color: #f8f9fb;
  box-sizing: border-box;
  padding: 20px;
  .aside-bar {
    width: 200px;
    float: left;
    .check-btn {
      margin: 10px auto 40px;
    }
    .el-button{
      border-radius: 0;
      width: 100px;
      height: 50px;
    }
    .list-title {
      height: 35px;
      margin: 0 auto;
      background-color: #28bdb9;
      color: white;
      text-align: center;
      font-size: 0.1rem;
      line-height: 35px;
      cursor: pointer;
    }
  }
  .check-wrap {
    position: relative;
    float: left;
    width: 920px;
    margin-left: 40px;
    background: #fff;
    box-shadow: 0px 1px 1px #dde0e7;
    padding: 20px;
    box-sizing: border-box;
    overflow: hidden;
    .item {
      height: 40px;
      line-height: 40px;
      margin-bottom: 22px;
      > * {float: left;}
      .item-title {
        width: 100px;
        text-align: right;
        margin-right: 20px;
      }
      .item-con {
        max-width: 70%;
      }
      &.check {
        text-align: center;
        margin-bottom: 50px;
        .item-con {max-width: 100%;float: none;}
      }
    }
    .check-status{
      position: absolute;
      top: -4px;
      right: -38px;
      width: 110px;
      height: 50px;
      color: #fff;
      line-height: 60px;
      font-size: 20px;
      transform: rotate(45deg);
      text-align: center;
      &.pass{background: #28bdb9;}
      &.nopass{ background: #f56c6c;}
      i {
        transform: rotate(-45deg);
      }
    }
  }
}

</style>



