    // 
    showhideSideBar() {
      let thisVue = this;
      $(window).scroll(function() {
        let scrollTop = $(document).scrollTop();
        if (scrollTop > 400) {
          thisVue.sideshowhide = true;
        } else {
          thisVue.sideshowhide = false;
        }
      });
    },
    // 
    fnGoTop() {
      $("html, body").animate({
        scrollTop: "0"
      });
    },
    // 
    handleSizeChange(val) {
      console.log(val);
    },
    // 
    handleCurrentChange(val) {
      console.log(val);
    },
    fnAddBank () {
      this.$router.push({
        "path": '/addbank'
      });
    },
    //
    searchQuestion (search, type = '') {
      console.log(search);
      this.searchQuestionList = [];
      let typeArr = this.questionList.filter(item => (item.qtype === type) ? true : false);
      this.searchQuestionList.push(typeArr);
      //console.log(this.searchQuestionList[0]);
    },
    // 
    fnEdit (questionid) {
      this.$router.push({
        "path": '/edit/' + questionid,
      });
    },
    // 
    customByUserMethod (userChooseForm) {
      this.$refs[userChooseForm].validate((valid) => {
          if (valid) {
            this.statusChoose.push(
              {
                qtype:this.userChooseForm.type,
                number:this.userChooseForm.number,
                grade:this.userChooseForm.grade
              }
            );
            this.dialogFormVisible = false;
            this.searchQuestion(null, this.userChooseForm.type);
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    },
    //
    openDetails (row) {
      this.searchQuestion(null, row.qtype);
      console.log(row);
    },
    // 
    fnReadCheckMsg (id) {
      this.$message({type: 'error', message: ''});
    },
    // 
    tranforQtype (obj) {
      let typeArr = this.bankTypeList.filter(item => (item.value === obj.qtype) ? true : false);
      return typeArr[0].name;
    },
    // 
    transformQuse (val) {
      let typeArr = this.qusepermiseList.filter((item) => {
        if (item.id === val) {
          return true;
        }
      });
      return typeArr[0].name;
    },
    // 
    transformComplexity (val) {
      let typeArr = this.complexityList.filter((item) => {
        if (item.value === val) {
          return true;
        }
      });
      return typeArr[0].name;
    },
    //
    recoverList (scoperow) {
      this.previewArr = this.previewArr.filter(item => {
        return (scoperow.id === item.id) ? false : true;
      });
      
      //
      this.showPartType(this.radioType);
      //
      this.searchQuestionList[0].push(scoperow);
      // 
      let isHave = false;
      for (let i = 0; i < this.searchPrevArr.length; i++) {
        if (this.searchPrevArr[i].qtype === scoperow.qtype) {
          isHave = true;
        }
      }
      if (!isHave) {
        this.addBankType = this.addBankType.filter(item => {
          return (item.value === scoperow.qtype) ? false : true;
        });
      }
      //console.log(11111111111, this.searchPrevArr['length']);
      // if (this.searchPrevArr['length'] === 0) {
      //   this.statusChoose = [];
      // }
    },
    //
    delContackForStatusChoose() {
      
    },
    //
    showPartType(type) {
      if (type === 'all') {
          this.searchPrevArr = this.previewArr;
        } else {
          this.searchPrevArr = this.previewArr.filter(item => (type === item.qtype) ? true : false);
      }
    },
    //push=>id,name
    pushArr (item, index) {
      let This = this;
      this.previewArr.push(item);
      //
      this.searchPrevArr = this.previewArr;
      //
      this.searchQuestionList[0].splice(index, 1);
      let newArr = this.bankTypeList.filter(obj => {
        let isHave = (function () {
          for (let i = 0; i < This.addBankType.length; i++) {
            if (This.addBankType[i].value === item.qtype) {
              return true;
            }
          }
          return false;
        })();
        return (obj.value === item.qtype && !isHave) ? true : false;
      });
      if (newArr.length) {
        this.addBankType.push(newArr[0]);
      }
      
    },
    //
    mySubmit() {
      this.$ajax.post(this.Const.apiurl, {
        module:'exam',
        controller:'Quiz',
        action:'handAddQuestion',
        questions:{'qtype':this.userChooseForm.type, 'score':this.userChooseForm.grade, 'question':this.questionList.id},
      }).then(res => {
        let resData = res.data;
        console.log('', resData);
        if (resData.status === 0) {
          //
        }
      });
    },
    //
    getQuestionData() {
      this.$ajax.post(this.Const.apiurl, {
        module: 'exam',
        controller: 'Question',
        action: 'questionList',
        course: this.Const.courseData.query.course,
        classroom: '',
        section: (this.search.selectedOptions[1]) ? this.search.selectedOptions[0] : '',
        sections: (this.search.selectedOptions[1]) ? this.search.selectedOptions[1] : '',
        qtype: this.search.bankType,
        kwords: this.search.searchCon,
        check: this.search.check,
        diff: this.search.complexity,
        usepermise: this.search.qusepermise,
        page: this.page.currentPage,
        rows: this.page.pageSize,
      }).then(res => {
        let resData = res.data;
        console.log('', res);
      })
    }
  },
  computed: {
    
  }
};
</script>

<style lang="scss">
.questbank {
  .aside {
    width: 195px;
    background: #fff;
    float: left;
    margin-right: 5px;
    .title {
      @include middelH(50px);
      @include setTitle;
    }
    .total-bank {
      height: 80px;
      background: #ff8a00;
      padding-left: 30px;
      color: #fff;
      span {
        @include middelH(40px);
      }
      i {
        font-size: 2.2rem;
        margin-right: 10px;
      }
    }
    .total-bankSecond {
      height: 80px;
      background: #382004;
      padding-left: 30px;
      color: #fff;
      span {
        @include middelH(40px);
      }
      i {
        font-size: 2.2rem;
        margin-right: 10px;
      }
    }
    .time {
      i {
        font-size: 1.8rem;
        margin-right: 10px;
      }
    }
    .addbank {
      position: relative;
      margin-top: 20px;
      @include middelH(60px);
      border: 1px solid #32b16c;
      box-sizing: border-box;
      color: #32b16c;
      cursor: pointer;
      i {
        position: absolute;
        top: 13px;
        left: 40px;
        font-size: 3rem;
      }
      span {
        position: absolute;
        top: 0;
        left: 80px;
      }
      &:hover {
        background: #32b16c;
        color: #fff;
      }
    }
    .sub {
      color: #ffffff;
      background-color:darkgreen;
    }
  }
  .wrap {
    width: 1000px;
    box-sizing: border-box;
    float: left;
    background: #fff;
    padding: 10px;
    .head-condition {
      padding: 20px;
      border-bottom: 1px solid gray;
      .item-condition {
        margin-right: 30px;
        float: left;
        margin-bottom: 20px;
        .el-input-group,
        .el-input {
          width: auto;
        }
      }
    }
  }
}
.cursorPoint {
  cursor: pointer;
  font-size: 18px;
}
.bank-list {
  margin-top: 20px;
  .optionbar {
    margin-bottom: 20px;
  }
  .bank-item {
    border: 1px solid #dcdfe6;
    margin-bottom: 10px;
    box-sizing: border-box;
    &:hover {
      border-color: #32b16c;
    }
  }
  .title {
    padding: 10px;
    background: #f2f2f2;
    .bank-stem {
      width: 75%;
      .timu {
        width: 95%;
        margin-left: 10px;
      }
    }
  }
  .bank-type {
    color: #32b16c;
    font-weight: bold;
  }
  .answer-box {
    padding: 10px 20px;
    ul {
      margin: 10px 0;
    }
    li {
      padding-left: 30px;
      float: left;
      width: 40%;
      text-indent: 10px;
      margin-bottom: 10px;
      > * {
        float: left;
      }
      .answer-item {
        width: 80%;
      }
    }
    .cankao-answer {
      color: #ff8a00;
    }
    .cankaodaan {
      width: 90%;
      margin: 10px auto;
      .can-title {
        color: #ff8a00;
      }
      > p {
        text-align: center;
      }
    }
    .panduan {
      width: 90%;
      margin: 10px auto;
      p {
        margin-bottom: 10px;
      }
    }
  }
  .comprehensive {
    padding-left: 80px;
    .answer-list {
      margin-bottom: 20px;
    }
  }
  .note {
    @include middelH(30px);
    margin-right: 20px;
    .success {
      color: #67c23a;
    }
    .danger {
      color: #e6a23c;
    }
    .error {
      color: #f56c6c;
      cursor: pointer;
    }
  }
  .pic-box {
    position: relative;
    height: 200px;
    background: #f2f2f2;
    .pic {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      margin: 0 auto;
      width: 350px;
    }
    .annotation {
      position: absolute;
      bottom: 10px;
      left: 0;
      width: 100%;
      text-align: center;
      font-size: 1.4rem;
      color: gray;
    }
  }
  .gf {
    margin: 10px;
    .gf-title {
      color: #32b16c;
    }
    .gf-content {
      display: inline-block;
    }
  }
}

.flow-side-box {
  position: fixed;
  margin-left: 990px;
  top: 60%;
  width: 60px;
  background: #838485;
  color: #fff;
  cursor: pointer;
  .go-top {
    height: 60px;
    i {
      @include middelH(60px);
      font-size: 3rem;
      display: block;
      text-align: center;
    }
    &:hover {
      background: #32b16c;
    }
  }
  .add-bank-btn {
    position: relative;
    height: 140px;
    i {
      position: absolute;
      top: 10px;
      left: 15px;
      display: block;
      font-size: 3rem;
    }
    span {
      position: absolute;
      top: 44px;
      left: 17px;
      width: 26px;
      display: block;
      text-align: center;
      line-height: 20px;
    }
    &:hover {
      background: #32b16c;
      color: #fff;
    }
  }
}
.paging {
  margin: 30px 0;
}
</style>



